.message {
  display: flex;
  max-width: 40%;
  margin-left: auto;
  flex-direction: column;
}

.imageAndText {
  display: flex;
  align-items: flex-end;
  flex-direction: row-reverse;
}

.imageAndText img {
  height: 70px; /*размер изоб*/
}

.text {
  position: relative;
  z-index: -1;
  background: #0066cc;
  color: #fff;
  padding: 10px 15px;
  -webkit-border-radius: 10px;
  -webkit-border-bottom-right-radius: 0;
  -moz-border-radius: 10px;
  -moz-border-radius-bottomright: 0;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  margin-right: 15px;
}

.text:after {
  content: "";
  position: absolute;
  left: 100%;
  bottom: 0em;
  width: 1.8em;
  height: 1.8em;
  border: 0.5em solid white;
  border-radius: 50%;
  background: #fff;
}

.name {
  display: flex;
  font-weight: bold;
  justify-content: right;
  margin-bottom: 10px;
  font-size: 26px;
}

.messageText {
  display: flex;
  justify-content: right;
  font-size: 24px;
}

.time {
  display: flex;
  justify-content: right;
  padding: 10px 15px 30px 0;
  font-weight: bold;
}
